import { useState, useEffect, useCallback, useMemo, useRef } from 'react';
import { collection, getDocs, getDoc, doc, updateDoc, addDoc, query, where, orderBy, serverTimestamp, writeBatch } from 'firebase/firestore';
import { db } from '../../firebase';
import { useNotification } from './NotificationSystem';
import { useAuth } from '../../context/AuthContext';
import { 
  FiCheckCircle, FiClock, FiAlertCircle, FiXCircle, FiEdit2, 
  FiUser, FiFileText, FiCalendar, FiFilter, FiSearch, FiRefreshCw,
  FiActivity, FiEye, FiTrendingUp, FiArrowRight, FiPlay, FiPause,
  FiCheck, FiX, FiCpu, FiZap, FiTarget, FiBell,
  FiGrid, FiList, FiMoreVertical, FiChevronDown, FiChevronUp,
  FiDownload, FiUpload, FiSettings, FiSliders, FiMaximize2,
  FiMinimize2, FiMenu, FiPhone, FiTablet, FiMonitor,
  FiTool, FiPackage, FiTruck, FiBarChart2, FiPlus, FiMinus
} from 'react-icons/fi';

// Machine operations with detailed configuration
const MACHINE_OPERATIONS = {
  HBL: { name: 'Horizontal Boring', icon: FiTool, color: 'blue' },
  VTL: { name: 'Vertical Turning', icon: FiCpu, color: 'purple' },
  Drilling: { name: 'Drilling', icon: FiZap, color: 'green' },
  VMC: { name: 'Vertical Machining', icon: FiTarget, color: 'indigo' }
};

// Operation Status Service (atomic & auditable)
const updatePOOperationStatusService = async (poId, operation, newStatus, user, notes = '') => {
  try {
    const poRef = doc(db, 'purchaseOrders', poId);
    const poSnap = await getDoc(poRef);
    if (!poSnap.exists()) throw new Error('Purchase order not found');

    const currentData = poSnap.data() || {};
    const currentStatus = currentData.operationsStatus?.[operation] || 'pending';

    // Build updated maps
    const updatedOperationsStatus = {
      ...(currentData.operationsStatus || {}),
      [operation]: newStatus,
    };

    // Calculate progress and derived status
    const totalOperations = Object.keys(MACHINE_OPERATIONS).length;
    const completedOperations = Object.values(updatedOperationsStatus).filter((s) => s === 'completed').length;
    const progress = Math.round((completedOperations / totalOperations) * 100);
    let derivedStatus = currentData.status || 'pending';
    if (completedOperations === totalOperations) derivedStatus = 'completed';
    else if (completedOperations > 0 || Object.values(updatedOperationsStatus).some((s) => s === 'in_progress')) derivedStatus = 'in_progress';

    const batch = writeBatch(db);

    // Build per-operation meta
    const baseMeta = {
      status: newStatus,
      notes: notes || '',
      updatedAt: serverTimestamp(),
      updatedBy: {
        uid: user?.uid || null,
        email: user?.email || null,
        name: user?.displayName || null,
      },
    };
    const extraMeta = {};
    if (newStatus === 'in_progress') extraMeta.startedAt = serverTimestamp();
    if (newStatus === 'completed') extraMeta.completedAt = serverTimestamp();

    batch.update(poRef, {
      operationsStatus: updatedOperationsStatus,
      [`operationsMeta.${operation}`]: { ...(currentData.operationsMeta?.[operation] || {}), ...baseMeta, ...extraMeta },
      status: derivedStatus,
      progress,
      updatedAt: serverTimestamp(),
    });

    // Propagate to related entities (relational updates)
    // 1) Delivery Challans linked by poId
    try {
      const dcsSnap = await getDocs(
        query(collection(db, 'deliveryChallans'), where('poId', '==', poId))
      );
      dcsSnap.forEach((dcDoc) => {
        batch.update(doc(db, 'deliveryChallans', dcDoc.id), {
          poStatus: derivedStatus,
          poProgress: progress,
          updatedAt: serverTimestamp(),
        });
      });
    } catch (e) {
      console.warn('DC propagation skipped/failed:', e?.message || e);
    }

    // 2) Job Assignments linked by poId or reference == poNumber
    const mapPOToJobStatus = (poStatus) => {
      switch (poStatus) {
        case 'completed':
          return 'completed';
        case 'in_progress':
          return 'in-progress';
        case 'pending':
        default:
          return 'assigned';
      }
    };

    try {
      const updates = [];
      // By poId
      const jobsByPoId = await getDocs(
        query(collection(db, 'jobAssignments'), where('poId', '==', poId))
      );
      jobsByPoId.forEach((j) => updates.push(j.id));

      // Fallback by reference == poNumber (if present)
      const poNumber = currentData.poNumber;
      if (updates.length === 0 && poNumber) {
        const jobsByRef = await getDocs(
          query(collection(db, 'jobAssignments'), where('reference', '==', poNumber))
        );
        jobsByRef.forEach((j) => updates.push(j.id));
      }

      const jobStatus = mapPOToJobStatus(derivedStatus);
      updates.forEach((jobId) => {
        batch.update(doc(db, 'jobAssignments', jobId), {
          status: jobStatus,
          updatedAt: serverTimestamp(),
        });
      });
    } catch (e) {
      console.warn('Job propagation skipped/failed:', e?.message || e);
    }

    // History record
    const historyRef = doc(collection(db, 'operationHistory'));
    batch.set(historyRef, {
      itemId: poId,
      itemType: 'po_operation',
      operation,
      oldStatus: currentStatus,
      newStatus,
      notes: notes || '',
      userId: user?.uid || null,
      userName: user?.displayName || user?.email || 'Unknown',
      timestamp: serverTimestamp(),
    });

    await batch.commit();
    return true;
  } catch (error) {
    console.error('Error in operation status service:', error);
    throw error;
  }
};

// Operation Tracker Component
const OperationTracker = ({ item, history, onClose, getStatusColor, getStatusIcon, getVendorName }) => {
  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4">
      <div className="bg-white rounded-xl shadow-xl max-w-4xl w-full max-h-[90vh] overflow-y-auto">
        <div className="p-6 border-b border-gray-200 flex justify-between items-center">
          <h2 className="text-xl font-bold text-gray-900">Operation History - {item.poNumber}</h2>
          <button onClick={onClose} className="text-gray-400 hover:text-gray-600">
            <FiX size={24} />
          </button>
        </div>
        
        <div className="p-6">
          <div className="mb-6 grid grid-cols-1 md:grid-cols-2 gap-4">
            <div>
              <h3 className="text-sm font-medium text-gray-500">Vendor</h3>
              <p className="text-lg font-medium">{getVendorName(item.vendorId)}</p>
            </div>
            <div>
              <h3 className="text-sm font-medium text-gray-500">Current Status</h3>
              <p className={`inline-flex items-center px-3 py-1 rounded-full text-sm font-medium ${getStatusColor(item.status)}`}>
                {getStatusIcon(item.status)}
                <span className="ml-1 capitalize">{item.status?.replace('_', ' ')}</span>
              </p>
            </div>
          </div>
          
          <h3 className="text-lg font-medium text-gray-900 mb-4">Operation Timeline</h3>
          
          {history.length > 0 ? (
            <div className="space-y-4">
              {history.map((record, index) => (
                <div key={record.id || index} className="flex">
                  <div className="flex flex-col items-center mr-4">
                    <div className={`w-3 h-3 rounded-full ${
                      record.newStatus === 'completed' ? 'bg-green-500' :
                      record.newStatus === 'in_progress' ? 'bg-blue-500' :
                      record.newStatus === 'pending' ? 'bg-yellow-500' : 'bg-gray-500'
                    }`}></div>
                    {index < history.length - 1 && (
                      <div className="w-0.5 h-16 bg-gray-300 my-1"></div>
                    )}
                  </div>
                  <div className="flex-1 pb-4">
                    <div className="flex justify-between">
                      <span className="text-sm font-medium text-gray-900">
                        {MACHINE_OPERATIONS[record.operation]?.name || record.operation}
                      </span>
                      <span className="text-sm text-gray-500">
                        {record.timestamp?.toDate?.().toLocaleString() || 'N/A'}
                      </span>
                    </div>
                    <p className="text-sm text-gray-600 mt-1">
                      Changed from <span className="font-medium">{record.oldStatus}</span> to <span className="font-medium">{record.newStatus}</span>
                      {record.notes && ` - ${record.notes}`}
                    </p>
                    <p className="text-xs text-gray-500 mt-1">By: {record.userName}</p>
                  </div>
                </div>
              ))}
            </div>
          ) : (
            <div className="text-center py-8 text-gray-500">
              No history available for this operation
            </div>
          )}
        </div>
        
        <div className="p-6 border-t border-gray-200 flex justify-end">
          <button
            onClick={onClose}
            className="px-4 py-2 bg-indigo-600 text-white rounded-lg hover:bg-indigo-700 transition-colors"
          >
            Close
          </button>
        </div>
      </div>
    </div>
  );
};

// Status Update Modal Component
const StatusUpdateModal = ({ isOpen, onClose, operation, currentStatus, onStatusUpdate }) => {
  const [notes, setNotes] = useState('');
  const [selectedStatus, setSelectedStatus] = useState(currentStatus);
  const [updating, setUpdating] = useState(false);

  useEffect(() => {
    if (isOpen) {
      setSelectedStatus(currentStatus);
      setNotes('');
    }
  }, [isOpen, currentStatus]);

  const handleSubmit = async () => {
    if (selectedStatus === currentStatus) {
      onClose();
      return;
    }

    setUpdating(true);
    try {
      await onStatusUpdate(selectedStatus, notes);
      onClose();
    } catch (error) {
      console.error('Error updating status:', error);
    } finally {
      setUpdating(false);
    }
  };

  if (!isOpen) return null;

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4">
      <div className="bg-white rounded-xl shadow-xl max-w-md w-full">
        <div className="p-6 border-b border-gray-200">
          <h2 className="text-xl font-bold text-gray-900">Update Operation Status</h2>
          <p className="text-sm text-gray-600 mt-1">
            {MACHINE_OPERATIONS[operation]?.name || operation}
          </p>
        </div>
        
        <div className="p-6 space-y-4">
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">Status</label>
            <div className="grid grid-cols-3 gap-2">
              <button
                onClick={() => setSelectedStatus('pending')}
                className={`p-3 rounded-lg border text-center ${
                  selectedStatus === 'pending'
                    ? 'bg-yellow-100 border-yellow-500 text-yellow-700'
                    : 'bg-gray-100 border-gray-300 text-gray-700 hover:bg-gray-200'
                }`}
              >
                <FiClock className="mx-auto mb-1" />
                <span className="text-xs">Pending</span>
              </button>
              <button
                onClick={() => setSelectedStatus('in_progress')}
                className={`p-3 rounded-lg border text-center ${
                  selectedStatus === 'in_progress'
                    ? 'bg-blue-100 border-blue-500 text-blue-700'
                    : 'bg-gray-100 border-gray-300 text-gray-700 hover:bg-gray-200'
                }`}
              >
                <FiPlay className="mx-auto mb-1" />
                <span className="text-xs">In Progress</span>
              </button>
              <button
                onClick={() => setSelectedStatus('completed')}
                className={`p-3 rounded-lg border text-center ${
                  selectedStatus === 'completed'
                    ? 'bg-green-100 border-green-500 text-green-700'
                    : 'bg-gray-100 border-gray-300 text-gray-700 hover:bg-gray-200'
                }`}
              >
                <FiCheckCircle className="mx-auto mb-1" />
                <span className="text-xs">Completed</span>
              </button>
            </div>
          </div>
          
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">Notes (Optional)</label>
            <textarea
              value={notes}
              onChange={(e) => setNotes(e.target.value)}
              placeholder="Add any notes about this status change..."
              className="w-full p-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-transparent"
              rows={3}
            />
          </div>
        </div>
        
        <div className="p-6 border-t border-gray-200 flex justify-end space-x-3">
          <button
            onClick={onClose}
            className="px-4 py-2 border border-gray-300 text-gray-700 rounded-lg hover:bg-gray-50 transition-colors"
            disabled={updating}
          >
            Cancel
          </button>
          <button
            onClick={handleSubmit}
            disabled={updating || selectedStatus === currentStatus}
            className="px-4 py-2 bg-indigo-600 text-white rounded-lg hover:bg-indigo-700 disabled:opacity-50 disabled:cursor-not-allowed transition-colors flex items-center"
          >
            {updating ? (
              <>
                <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white mr-2"></div>
                Updating...
              </>
            ) : (
              'Update Status'
            )}
          </button>
        </div>
      </div>
    </div>
  );
};

export default function StatusManager() {
  const { showSuccess, showError } = useNotification();
  const { currentUser } = useAuth();
  const [jobs, setJobs] = useState([]);
  const [pos, setPOs] = useState([]);
  const [vendors, setVendors] = useState([]);
  const [loading, setLoading] = useState(true);
  const [updating, setUpdating] = useState({});
  const [activeTab, setActiveTab] = useState('operations');
  const [selectedItem, setSelectedItem] = useState(null);
  const [showTracker, setShowTracker] = useState(false);
  const [operationHistory, setOperationHistory] = useState([]);
  const [filters, setFilters] = useState({
    status: 'all',
    operation: 'all',
    search: '',
    dateRange: 'all',
    vendor: 'all'
  });
  const [viewMode, setViewMode] = useState('table');
  const [selectedItems, setSelectedItems] = useState([]);
  const [isMobile, setIsMobile] = useState(false);
  const [sortConfig, setSortConfig] = useState({ key: 'createdAt', direction: 'desc' });
  const [autoRefresh, setAutoRefresh] = useState(false);
  const refreshIntervalRef = useRef(null);
  const [completedOperations, setCompletedOperations] = useState([]);
  const [vendorPerformance, setVendorPerformance] = useState({});
  const [statusUpdateModal, setStatusUpdateModal] = useState({
    isOpen: false,
    poId: null,
    operation: null,
    currentStatus: null
  });

  useEffect(() => {
    try {
      const saved = localStorage.getItem('statusManagerFilters');
      if (saved) {
        const parsed = JSON.parse(saved);
        setFilters(prev => ({ ...prev, ...parsed }));
      }
    } catch {}

    fetchData();
    
    // Check if mobile
    const checkMobile = () => {
      setIsMobile(window.innerWidth < 768);
    };
    
    checkMobile();
    window.addEventListener('resize', checkMobile);
    
    return () => {
      window.removeEventListener('resize', checkMobile);
      if (refreshIntervalRef.current) {
        clearInterval(refreshIntervalRef.current);
      }
    };
  }, []);

  useEffect(() => {
    if (autoRefresh) {
      if (refreshIntervalRef.current) {
        clearInterval(refreshIntervalRef.current);
      }
      refreshIntervalRef.current = setInterval(fetchData, 30000);
    } else {
      if (refreshIntervalRef.current) {
        clearInterval(refreshIntervalRef.current);
        refreshIntervalRef.current = null;
      }
    }
    return () => {
      if (refreshIntervalRef.current) {
        clearInterval(refreshIntervalRef.current);
      }
    };
  }, [autoRefresh]);

  useEffect(() => {
    try {
      localStorage.setItem('statusManagerFilters', JSON.stringify(filters));
    } catch {}
  }, [filters]);

  // Calculate vendor performance metrics
  useEffect(() => {
    if (pos.length > 0 && vendors.length > 0) {
      const performance = {};
      
      vendors.forEach(vendor => {
        const vendorPOs = pos.filter(po => po.vendorId === vendor.id);
        const completedOps = [];
        
        vendorPOs.forEach(po => {
          Object.entries(po.operationsStatus || {}).forEach(([operation, status]) => {
            if (status === 'completed') {
              completedOps.push({
                poNumber: po.poNumber,
                operation,
                completionDate: po.updatedAt || po.createdAt,
                vendor: vendor.name
              });
            }
          });
        });
        
        performance[vendor.id] = {
          name: vendor.name,
          totalPOs: vendorPOs.length,
          totalOperations: completedOps.length,
          completedOperations: completedOps,
          efficiency: vendorPOs.length > 0 
            ? (completedOps.length / (vendorPOs.length * Object.keys(MACHINE_OPERATIONS).length)) * 100 
            : 0,
          averageCompletionTime: calculateAverageCompletionTime(vendorPOs)
        };
      });
      
      setVendorPerformance(performance);
      setCompletedOperations(
        Object.values(performance)
          .flatMap(v => v.completedOperations)
          .sort((a, b) => new Date(b.completionDate) - new Date(a.completionDate))
      );
    }
  }, [pos, vendors]);

  const calculateAverageCompletionTime = (vendorPOs) => {
    let totalTime = 0;
    let count = 0;
    
    vendorPOs.forEach(po => {
      Object.entries(po.operationsMeta || {}).forEach(([operation, meta]) => {
        if (meta.completedAt && meta.startedAt) {
          const startTime = meta.startedAt.toDate ? meta.startedAt.toDate() : new Date(meta.startedAt);
          const endTime = meta.completedAt.toDate ? meta.completedAt.toDate() : new Date(meta.completedAt);
          totalTime += (endTime - startTime);
          count++;
        }
      });
    });
    
    return count > 0 ? totalTime / count : 0;
  };

  const fetchData = async () => {
    try {
      setLoading(true);
      const [jobsSnapshot, posSnapshot, vendorsSnapshot] = await Promise.all([
        getDocs(collection(db, 'jobAssignments')),
        getDocs(query(collection(db, 'purchaseOrders'), orderBy('createdAt', 'desc'))),
        getDocs(collection(db, 'vendors'))
      ]);
      
      const jobsData = [];
      jobsSnapshot.forEach((doc) => {
        jobsData.push({ id: doc.id, ...doc.data() });
      });
      
      const posData = [];
      posSnapshot.forEach((doc) => {
        const data = doc.data();
        posData.push({ 
          id: doc.id, 
          ...data,
          // Ensure operationsStatus exists and has all operations
          operationsStatus: {
            ...Object.keys(MACHINE_OPERATIONS).reduce((acc, op) => ({ 
              ...acc, 
              [op]: data.operationsStatus?.[op] || 'pending' 
            }), {}),
            ...(data.operationsStatus || {})
          }
        });
      });
      
      const vendorsData = [];
      vendorsSnapshot.forEach((doc) => {
        vendorsData.push({ id: doc.id, ...doc.data() });
      });
      
      setJobs(jobsData);
      setPOs(posData);
      setVendors(vendorsData);
    } catch (error) {
      console.error('Error fetching data:', error);
      showError('Failed to load data');
    } finally {
      setLoading(false);
    }
  };

  const updatePOOperationStatus = async (poId, operation, newStatus, notes = '') => {
    try {
      setUpdating(prev => ({ ...prev, [`${poId}_${operation}`]: true }));
      const po = pos.find(p => p.id === poId);
      const oldStatus = po?.operationsStatus?.[operation] || 'pending';

      // Call centralized atomic service with authenticated user context
      await updatePOOperationStatusService(poId, operation, newStatus, currentUser || {}, notes);

      // Update local state then compute derived overall status inline
      setPOs(prev => prev.map(p => {
        if (p.id !== poId) return p;
        const updatedOps = { ...(p.operationsStatus || {}), [operation]: newStatus };
        
        // Calculate completion percentage
        const totalOperations = Object.keys(MACHINE_OPERATIONS).length;
        const completedOperations = Object.values(updatedOps).filter(status => status === 'completed').length;
        const progress = Math.round((completedOperations / totalOperations) * 100);
        
        // Determine overall status
        let derivedStatus = p.status || 'pending';
        if (completedOperations === totalOperations) derivedStatus = 'completed';
        else if (completedOperations > 0 || Object.values(updatedOps).some(status => status === 'in_progress')) {
          derivedStatus = 'in_progress';
        }
        
        return { 
          ...p, 
          operationsStatus: updatedOps, 
          status: derivedStatus,
          progress 
        };
      }));

      showSuccess(`Updated ${MACHINE_OPERATIONS[operation].name} from ${oldStatus} to ${newStatus}`);
    } catch (error) {
      console.error('Error updating operation status:', error);
      showError('Failed to update operation status');
    } finally {
      setUpdating(prev => ({ ...prev, [`${poId}_${operation}`]: false }));
    }
  };

  const openStatusUpdateModal = (poId, operation, currentStatus) => {
    setStatusUpdateModal({
      isOpen: true,
      poId,
      operation,
      currentStatus
    });
  };

  const closeStatusUpdateModal = () => {
    setStatusUpdateModal({
      isOpen: false,
      poId: null,
      operation: null,
      currentStatus: null
    });
  };

  const handleStatusUpdate = async (newStatus, notes) => {
    const { poId, operation } = statusUpdateModal;
    await updatePOOperationStatus(poId, operation, newStatus, notes);
  };

  const getVendorName = (vendorId) => {
    const vendor = vendors.find(v => v.id === vendorId);
    return vendor ? vendor.name : 'Unknown Vendor';
  };

  const getStatusColor = (status) => {
    switch (status) {
      case 'completed':
        return 'bg-green-100 text-green-800';
      case 'in_progress':
        return 'bg-blue-100 text-blue-800';
      case 'pending':
        return 'bg-yellow-100 text-yellow-800';
      case 'rejected':
      case 'cancelled':
        return 'bg-red-100 text-red-800';
      default:
        return 'bg-gray-100 text-gray-800';
    }
  };

  const getStatusIcon = (status) => {
    switch (status) {
      case 'completed':
        return <FiCheckCircle className="text-green-500" />;
      case 'in_progress':
        return <FiPlay className="text-blue-500" />;
      case 'pending':
        return <FiClock className="text-yellow-500" />;
      case 'rejected':
      case 'cancelled':
        return <FiX className="text-red-500" />;
      default:
        return <FiAlertCircle className="text-gray-500" />;
    }
  };

  const getOperationIcon = (operation) => {
    const OperationIcon = MACHINE_OPERATIONS[operation]?.icon || FiTool;
    return <OperationIcon className={`text-${MACHINE_OPERATIONS[operation]?.color}-500`} />;
  };

  const fetchOperationHistory = async (itemId, itemType) => {
    try {
      const historyQuery = query(
        collection(db, 'operationHistory'),
        where('itemId', '==', itemId),
        where('itemType', '==', itemType),
        orderBy('timestamp', 'desc')
      );
      const snapshot = await getDocs(historyQuery);
      const history = [];
      snapshot.forEach(doc => {
        history.push({ id: doc.id, ...doc.data() });
      });
      setOperationHistory(history);
    } catch (error) {
      console.error('Error fetching operation history:', error);
      setOperationHistory([]);
    }
  };

  const openTracker = (item, type) => {
    setSelectedItem({ ...item, type });
    setShowTracker(true);
    fetchOperationHistory(item.id, type);
  };

  const filteredPOs = useMemo(() => {
    let filtered = pos.filter(po => {
      const matchesStatus = filters.status === 'all' || po.status === filters.status;
      const matchesVendor = filters.vendor === 'all' || po.vendorId === filters.vendor;
      const matchesOperation = filters.operation === 'all' || 
        (po.operationsStatus && filters.operation in po.operationsStatus);
      const matchesSearch = po.poNumber?.toLowerCase().includes(filters.search.toLowerCase()) ||
                           getVendorName(po.vendorId).toLowerCase().includes(filters.search.toLowerCase());
      
      let matchesDate = true;
      if (filters.dateRange !== 'all' && po.createdAt) {
        const poDate = po.createdAt.toDate ? po.createdAt.toDate() : new Date(po.createdAt);
        const now = new Date();
        const daysDiff = Math.floor((now - poDate) / (1000 * 60 * 60 * 24));
        
        switch (filters.dateRange) {
          case 'today': matchesDate = daysDiff === 0; break;
          case 'week': matchesDate = daysDiff <= 7; break;
          case 'month': matchesDate = daysDiff <= 30; break;
          default: matchesDate = true;
        }
      }
      
      return matchesStatus && matchesVendor && matchesOperation && matchesSearch && matchesDate;
    });
    
    return filtered.sort((a, b) => {
      const aVal = a[sortConfig.key];
      const bVal = b[sortConfig.key];
      
      if (sortConfig.key === 'progress') {
        return sortConfig.direction === 'asc' ? aVal - bVal : bVal - aVal;
      }
      
      if (aVal < bVal) return sortConfig.direction === 'asc' ? -1 : 1;
      if (aVal > bVal) return sortConfig.direction === 'asc' ? 1 : -1;
      return 0;
    });
  }, [pos, filters, sortConfig]);

  const uniqueVendors = useMemo(() => {
    const vendorIds = [...new Set(pos.map(p => p.vendorId))];
    return vendorIds.map(id => ({ id, name: getVendorName(id) })).filter(v => v.name !== 'Unknown Vendor');
  }, [pos, vendors]);

  if (loading) {
    return (
      <div className="flex justify-center items-center h-64">
        <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-indigo-500"></div>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="bg-white rounded-xl shadow-sm border border-gray-200 p-6">
        <div className="flex flex-col lg:flex-row lg:justify-between lg:items-start space-y-4 lg:space-y-0">
          <div className="flex items-center space-x-4">
            <div className="w-12 h-12 bg-gradient-to-br from-indigo-500 to-purple-600 rounded-xl flex items-center justify-center">
              <FiActivity className="text-white" size={24} />
            </div>
            <div>
              <h1 className="text-3xl font-bold text-gray-900">Machine Operations Manager</h1>
              <p className="text-base text-gray-600 mt-1">Track and manage all machine operations</p>
            </div>
          </div>
          
          <div className="flex items-center space-x-3">
            <div className="flex items-center space-x-2 text-sm text-gray-600">
              <FiBell className="text-indigo-500" size={16} />
              <span>Live Updates</span>
              <div className={`w-2 h-2 rounded-full animate-pulse ${
                autoRefresh ? 'bg-green-400' : 'bg-gray-400'
              }`} />
            </div>
            
            <button
              onClick={() => setAutoRefresh(!autoRefresh)}
              className={`p-2 rounded-lg transition-all duration-200 ${
                autoRefresh 
                  ? 'bg-green-100 text-green-600 hover:bg-green-200' 
                  : 'bg-gray-100 text-gray-600 hover:bg-gray-200'
              }`}
              title={autoRefresh ? 'Disable auto-refresh' : 'Enable auto-refresh'}
            >
              <FiRefreshCw className={autoRefresh ? 'animate-spin' : ''} size={16} />
            </button>
            
            <button
              onClick={fetchData}
              className="inline-flex items-center px-4 py-2 bg-gradient-to-r from-indigo-600 to-purple-600 text-white rounded-lg hover:from-indigo-700 hover:to-purple-700 transition-all duration-200 shadow-lg hover:shadow-xl"
            >
              <FiRefreshCw className="mr-2" size={16} />
              Refresh Data
            </button>
          </div>
        </div>
        
        {/* Stats Overview */}
        <div className="grid grid-cols-1 md:grid-cols-4 gap-4 mt-6">
          <div className="bg-gradient-to-br from-blue-50 to-indigo-50 p-4 rounded-lg border border-blue-100">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-blue-600 text-sm font-medium">Total POs</p>
                <p className="text-2xl font-bold text-blue-900">{pos.length}</p>
              </div>
              <FiFileText className="text-blue-500" size={24} />
            </div>
          </div>
          
          <div className="bg-gradient-to-br from-green-50 to-emerald-50 p-4 rounded-lg border border-green-100">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-green-600 text-sm font-medium">Completed Ops</p>
                <p className="text-2xl font-bold text-green-900">
                  {completedOperations.length}
                </p>
              </div>
              <FiCheckCircle className="text-green-500" size={24} />
            </div>
          </div>
          
          <div className="bg-gradient-to-br from-yellow-50 to-orange-50 p-4 rounded-lg border border-yellow-100">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-yellow-600 text-sm font-medium">In Progress</p>
                <p className="text-2xl font-bold text-yellow-900">
                  {pos.filter(po => po.status === 'in_progress').length}
                </p>
              </div>
              <FiPlay className="text-yellow-500" size={24} />
            </div>
          </div>
          
          <div className="bg-gradient-to-br from-purple-50 to-pink-50 p-4 rounded-lg border border-purple-100">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-purple-600 text-sm font-medium">Active Vendors</p>
                <p className="text-2xl font-bold text-purple-900">{uniqueVendors.length}</p>
              </div>
              <FiUser className="text-purple-500" size={24} />
            </div>
          </div>
        </div>
      </div>

      {/* Tabs */}
      <div className="bg-white rounded-xl shadow-sm border border-gray-200 p-2">
        <nav className="flex space-x-2">
          {['operations', 'vendors', 'completed'].map(tab => (
            <button
              key={tab}
              onClick={() => setActiveTab(tab)}
              className={`flex-1 py-3 px-4 rounded-lg font-medium text-sm transition-all duration-200 ${
                activeTab === tab
                  ? 'bg-gradient-to-r from-indigo-500 to-purple-600 text-white shadow-lg'
                  : 'text-gray-600 hover:text-gray-900 hover:bg-gray-50'
              }`}
            >
              <div className="flex items-center justify-center space-x-2">
                {tab === 'operations' && <FiActivity size={16} />}
                {tab === 'vendors' && <FiBarChart2 size={16} />}
                {tab === 'completed' && <FiCheckCircle size={16} />}
                <span className="capitalize">{tab}</span>
              </div>
            </button>
          ))}
        </nav>
      </div>

      {/* Main Content Area */}
      <div className="grid grid-cols-1 lg:grid-cols-4 gap-6">
        {/* Filters Sidebar */}
        <aside className="lg:col-span-1 space-y-4">
          <div className="bg-white p-4 rounded-lg shadow-sm border border-gray-200 sticky top-4">
            <h3 className="text-lg font-semibold text-gray-900 mb-4">Filters</h3>
            
            <div className="space-y-4">
              <div className="relative">
                <FiSearch className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400" size={16} />
                <input
                  type="text"
                  placeholder="Search POs or vendors..."
                  value={filters.search}
                  onChange={(e) => setFilters(prev => ({ ...prev, search: e.target.value }))}
                  className="w-full pl-10 pr-4 py-2 text-sm border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-transparent"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">Status</label>
                <select
                  value={filters.status}
                  onChange={(e) => setFilters(prev => ({ ...prev, status: e.target.value }))}
                  className="w-full px-3 py-2 text-sm border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-transparent"
                >
                  <option value="all">All Status</option>
                  <option value="pending">Pending</option>
                  <option value="in_progress">In Progress</option>
                  <option value="completed">Completed</option>
                </select>
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">Operation</label>
                <select
                  value={filters.operation}
                  onChange={(e) => setFilters(prev => ({ ...prev, operation: e.target.value }))}
                  className="w-full px-3 py-2 text-sm border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-transparent"
                >
                  <option value="all">All Operations</option>
                  {Object.keys(MACHINE_OPERATIONS).map(op => (
                    <option key={op} value={op}>{MACHINE_OPERATIONS[op].name}</option>
                  ))}
                </select>
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">Vendor</label>
                <select
                  value={filters.vendor}
                  onChange={(e) => setFilters(prev => ({ ...prev, vendor: e.target.value }))}
                  className="w-full px-3 py-2 text-sm border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-transparent"
                >
                  <option value="all">All Vendors</option>
                  {uniqueVendors.map(vendor => (
                    <option key={vendor.id} value={vendor.id}>{vendor.name}</option>
                  ))}
                </select>
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">Time Range</label>
                <select
                  value={filters.dateRange}
                  onChange={(e) => setFilters(prev => ({ ...prev, dateRange: e.target.value }))}
                  className="w-full px-3 py-2 text-sm border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-transparent"
                >
                  <option value="all">All Time</option>
                  <option value="today">Today</option>
                  <option value="week">This Week</option>
                  <option value="month">This Month</option>
                </select>
              </div>

              <div className="flex items-center justify-between pt-2">
                <span className="text-xs text-gray-500">{filteredPOs.length} POs</span>
                <button
                  className="text-xs text-indigo-600 hover:text-indigo-800"
                  onClick={() => setFilters({ status: 'all', operation: 'all', search: '', dateRange: 'all', vendor: 'all' })}
                >
                  Clear Filters
                </button>
              </div>
            </div>
          </div>

          {/* Vendor Performance Summary */}
          {activeTab !== 'vendors' && (
            <div className="bg-white p-4 rounded-lg shadow-sm border border-gray-200">
              <h3 className="text-lg font-semibold text-gray-900 mb-4">Vendor Performance</h3>
              <div className="space-y-4">
                {Object.entries(vendorPerformance)
                  .sort(([, a], [, b]) => b.efficiency - a.efficiency)
                  .slice(0, 3)
                  .map(([vendorId, data]) => (
                    <div key={vendorId} className="flex items-center justify-between">
                      <div className="flex-1 min-w-0">
                        <p className="text-sm font-medium text-gray-900 truncate">{data.name}</p>
                        <p className="text-xs text-gray-500">{data.totalOperations} completed operations</p>
                      </div>
                      <div className="flex items-center">
                        <div className="w-16 bg-gray-200 rounded-full h-2 mr-2">
                          <div 
                            className="bg-green-600 h-2 rounded-full" 
                            style={{ width: `${Math.min(data.efficiency, 100)}%` }}
                          />
                        </div>
                        <span className="text-xs font-medium text-gray-700">
                          {Math.round(data.efficiency)}%
                        </span>
                      </div>
                    </div>
                  ))}
                <button 
                  onClick={() => setActiveTab('vendors')}
                  className="w-full text-center text-sm text-indigo-600 hover:text-indigo-800 mt-2"
                >
                  View all vendors 
                </button>
              </div>
            </div>
          )}
        </aside>

        {/* Main Content */}
        <section className="lg:col-span-3">
          {activeTab === 'operations' && (
            <OperationsTable 
              pos={filteredPOs}
              vendors={vendors}
              updating={updating}
              onUpdateOperationStatus={updatePOOperationStatus}
              onOpenTracker={openTracker}
              onOpenStatusUpdateModal={openStatusUpdateModal}
              getVendorName={getVendorName}
              getStatusColor={getStatusColor}
              getStatusIcon={getStatusIcon}
              getOperationIcon={getOperationIcon}
            />
          )}

          {activeTab === 'vendors' && (
            <VendorsPerformance 
              vendorPerformance={vendorPerformance}
              getVendorName={getVendorName}
            />
          )}

          {activeTab === 'completed' && (
            <CompletedOperations 
              completedOperations={completedOperations}
              vendors={vendors}
              getVendorName={getVendorName}
            />
          )}
        </section>
      </div>

      {/* Operation Tracker Modal */}
      {showTracker && selectedItem && (
        <OperationTracker
          item={selectedItem}
          history={operationHistory}
          onClose={() => {
            setShowTracker(false);
            setSelectedItem(null);
            fetchData();
          }}
          getStatusColor={getStatusColor}
          getStatusIcon={getStatusIcon}
          getVendorName={getVendorName}
        />
      )}

      {/* Status Update Modal */}
      <StatusUpdateModal
        isOpen={statusUpdateModal.isOpen}
        onClose={closeStatusUpdateModal}
        operation={statusUpdateModal.operation}
        currentStatus={statusUpdateModal.currentStatus}
        onStatusUpdate={handleStatusUpdate}
      />
    </div>
  );
}

// Operations Table Component
function OperationsTable({ pos, vendors, updating, onUpdateOperationStatus, onOpenTracker, onOpenStatusUpdateModal, getVendorName, getStatusColor, getStatusIcon, getOperationIcon }) {
  const [expandedRow, setExpandedRow] = useState(null);
  
  const toggleRowExpansion = (poId) => {
    setExpandedRow(expandedRow === poId ? null : poId);
  };

  const handleStatusChange = (poId, operation, newStatus) => {
    onUpdateOperationStatus(poId, operation, newStatus, `Status changed to ${newStatus}`);
  };

  return (
    <div className="bg-white rounded-lg shadow-sm border border-gray-200 overflow-hidden">
      <div className="overflow-x-auto">
        <table className="min-w-full divide-y divide-gray-200">
          <thead className="bg-gray-50">
            <tr>
              <th scope="col" className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                PO Details
              </th>
              <th scope="col" className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Vendor
              </th>
              <th scope="col" className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Progress
              </th>
              <th scope="col" className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Status
              </th>
              <th scope="col" className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Actions
              </th>
            </tr>
          </thead>
          <tbody className="bg-white divide-y divide-gray-200">
            {pos.map((po) => (
              <>
                <tr key={po.id} className="hover:bg-gray-50 cursor-pointer" onClick={() => toggleRowExpansion(po.id)}>
                  <td className="px-4 py-4 whitespace-nowrap">
                    <div className="flex items-center">
                      <div className="ml-4">
                        <div className="text-sm font-medium text-gray-900">{po.poNumber}</div>
                        <div className="text-sm text-gray-500">
                          {po.createdAt?.toDate?.()?.toLocaleDateString() || 'N/A'}
                        </div>
                      </div>
                    </div>
                  </td>
                  <td className="px-4 py-4 whitespace-nowrap">
                    <div className="text-sm text-gray-900">{getVendorName(po.vendorId)}</div>
                  </td>
                  <td className="px-4 py-4 whitespace-nowrap">
                    <div className="flex items-center">
                      <div className="w-32 bg-gray-200 rounded-full h-2.5 mr-4">
                        <div 
                          className="bg-indigo-600 h-2.5 rounded-full" 
                          style={{ width: `${po.progress || 0}%` }}
                        />
                      </div>
                      <span className="text-sm font-medium text-gray-700">
                        {po.progress || 0}%
                      </span>
                    </div>
                  </td>
                  <td className="px-4 py-4 whitespace-nowrap">
                    <span className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium ${getStatusColor(po.status)}`}>
                      {getStatusIcon(po.status)}
                      <span className="ml-1 capitalize">{po.status?.replace('_', ' ')}</span>
                    </span>
                  </td>
                  <td className="px-4 py-4 whitespace-nowrap text-sm font-medium">
                    <div className="flex space-x-2">
                      <button
                        onClick={(e) => {
                          e.stopPropagation();
                          onOpenTracker(po, 'po');
                        }}
                        className="text-indigo-600 hover:text-indigo-900"
                      >
                        View Details
                      </button>
                    </div>
                  </td>
                </tr>
                
                {expandedRow === po.id && (
                  <tr className="bg-gray-50">
                    <td colSpan={5} className="px-4 py-4">
                      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
                        {Object.entries(MACHINE_OPERATIONS).map(([opKey, opConfig]) => {
                          const status = po.operationsStatus?.[opKey] || 'pending';
                          const OperationIcon = opConfig.icon;
                          const isUpdating = updating[`${po.id}_${opKey}`];
                          
                          return (
                            <div key={opKey} className="bg-white rounded-lg p-4 shadow-sm border border-gray-200">
                              <div className="flex items-center justify-between mb-3">
                                <div className="flex items-center">
                                  <OperationIcon className={`text-${opConfig.color}-500 mr-2`} />
                                  <div className="text-sm font-medium text-gray-900">{opConfig.name}</div>
                                </div>
                                <span className={`inline-flex items-center px-2 py-1 rounded-full text-xs font-medium ${getStatusColor(status)}`}>
                                  {getStatusIcon(status)}
                                  <span className="ml-1 capitalize">{status.replace('_', ' ')}</span>
                                </span>
                              </div>
                              
                              <div className="flex flex-col space-y-2">
                                <button
                                  onClick={() => onOpenStatusUpdateModal(po.id, opKey, status)}
                                  className="w-full py-2 px-3 bg-indigo-600 text-white rounded-lg hover:bg-indigo-700 transition-colors text-sm font-medium"
                                >
                                  Update Status
                                </button>
                                
                                <div className="flex space-x-1">
                                  <button
                                    onClick={() => handleStatusChange(po.id, opKey, 'pending')}
                                    disabled={isUpdating || status === 'pending'}
                                    className={`flex-1 py-1 px-2 text-xs rounded ${
                                      status === 'pending' 
                                        ? 'bg-yellow-100 text-yellow-800 border border-yellow-300' 
                                        : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                                    } ${isUpdating ? 'opacity-50 cursor-not-allowed' : ''}`}
                                  >
                                    {isUpdating && status === 'pending' ? '...' : 'Pending'}
                                  </button>
                                  
                                  <button
                                    onClick={() => handleStatusChange(po.id, opKey, 'in_progress')}
                                    disabled={isUpdating || status === 'in_progress'}
                                    className={`flex-1 py-1 px-2 text-xs rounded ${
                                      status === 'in_progress' 
                                        ? 'bg-blue-100 text-blue-800 border border-blue-300' 
                                        : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                                    } ${isUpdating ? 'opacity-50 cursor-not-allowed' : ''}`}
                                  >
                                    {isUpdating && status === 'in_progress' ? '...' : 'Start'}
                                  </button>
                                  
                                  <button
                                    onClick={() => handleStatusChange(po.id, opKey, 'completed')}
                                    disabled={isUpdating || status === 'completed'}
                                    className={`flex-1 py-1 px-2 text-xs rounded ${
                                      status === 'completed' 
                                        ? 'bg-green-100 text-green-800 border border-green-300' 
                                        : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                                    } ${isUpdating ? 'opacity-50 cursor-not-allowed' : ''}`}
                                  >
                                    {isUpdating && status === 'completed' ? '...' : 'Complete'}
                                  </button>
                                </div>
                              </div>
                            </div>
                          );
                        })}
                      </div>
                    </td>
                  </tr>
                )}
              </>
            ))}
          </tbody>
        </table>
        
        {pos.length === 0 && (
          <div className="text-center py-12">
            <FiFileText className="mx-auto h-12 w-12 text-gray-400" />
            <h3 className="mt-2 text-sm font-medium text-gray-900">No purchase orders found</h3>
            <p className="mt-1 text-sm text-gray-500">No purchase orders match your current filters.</p>
          </div>
        )}
      </div>
    </div>
  );
}

// Vendor Performance Component
function VendorsPerformance({ vendorPerformance, getVendorName }) {
  const formatTime = (milliseconds) => {
    if (!milliseconds) return 'N/A';
    
    const hours = Math.floor(milliseconds / (1000 * 60 * 60));
    const minutes = Math.floor((milliseconds % (1000 * 60 * 60)) / (1000 * 60));
    
    if (hours > 0) {
      return `${hours}h ${minutes}m`;
    }
    return `${minutes}m`;
  };

  return (
    <div className="bg-white rounded-lg shadow-sm border border-gray-200 overflow-hidden">
      <div className="px-6 py-4 border-b border-gray-200">
        <h3 className="text-lg font-medium text-gray-900">Vendor Performance</h3>
        <p className="mt-1 text-sm text-gray-500">Efficiency based on completed operations</p>
      </div>
      <div className="overflow-x-auto">
        <table className="min-w-full divide-y divide-gray-200">
          <thead className="bg-gray-50">
            <tr>
              <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Vendor
              </th>
              <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Total POs
              </th>
              <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Completed Operations
              </th>
              <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Efficiency
              </th>
              <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Avg. Completion Time
              </th>
            </tr>
          </thead>
          <tbody className="bg-white divide-y divide-gray-200">
            {Object.entries(vendorPerformance)
              .sort(([, a], [, b]) => b.efficiency - a.efficiency)
              .map(([vendorId, data]) => (
                <tr key={vendorId} className="hover:bg-gray-50">
                  <td className="px-6 py-4 whitespace-nowrap">
                    <div className="text-sm font-medium text-gray-900">{data.name}</div>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap">
                    <div className="text-sm text-gray-900">{data.totalPOs}</div>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap">
                    <div className="text-sm text-gray-900">{data.totalOperations}</div>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap">
                    <div className="flex items-center">
                      <div className="w-32 bg-gray-200 rounded-full h-2.5 mr-4">
                        <div 
                          className="bg-green-600 h-2.5 rounded-full" 
                          style={{ width: `${Math.min(data.efficiency, 100)}%` }}
                        />
                      </div>
                      <span className="text-sm font-medium text-gray-700">
                        {Math.round(data.efficiency)}%
                      </span>
                    </div>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap">
                    <div className="text-sm text-gray-500">
                      {formatTime(data.averageCompletionTime)}
                    </div>
                  </td>
                </tr>
              ))}
          </tbody>
        </table>
        
        {Object.keys(vendorPerformance).length === 0 && (
          <div className="text-center py-12">
            <FiUser className="mx-auto h-12 w-12 text-gray-400" />
            <h3 className="mt-2 text-sm font-medium text-gray-900">No vendor data available</h3>
            <p className="mt-1 text-sm text-gray-500">Vendor performance data will appear here once operations are completed.</p>
          </div>
        )}
      </div>
    </div>
  );
}

// Completed Operations Component
function CompletedOperations({ completedOperations, getVendorName }) {
  return (
    <div className="bg-white rounded-lg shadow-sm border border-gray-200 overflow-hidden">
      <div className="px-6 py-4 border-b border-gray-200">
        <h3 className="text-lg font-medium text-gray-900">Completed Operations</h3>
        <p className="mt-1 text-sm text-gray-500">All machine operations that have been completed</p>
      </div>
      <div className="overflow-x-auto">
        <table className="min-w-full divide-y divide-gray-200">
          <thead className="bg-gray-50">
            <tr>
              <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                PO Number
              </th>
              <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Operation
              </th>
              <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Vendor
              </th>
              <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Completed On
              </th>
            </tr>
          </thead>
          <tbody className="bg-white divide-y divide-gray-200">
            {completedOperations.map((op, index) => (
              <tr key={index} className="hover:bg-gray-50">
                <td className="px-6 py-4 whitespace-nowrap">
                  <div className="text-sm font-medium text-gray-900">{op.poNumber}</div>
                </td>
                <td className="px-6 py-4 whitespace-nowrap">
                  <div className="text-sm text-gray-900">
                    {MACHINE_OPERATIONS[op.operation]?.name || op.operation}
                  </div>
                </td>
                <td className="px-6 py-4 whitespace-nowrap">
                  <div className="text-sm text-gray-900">{op.vendor}</div>
                </td>
                <td className="px-6 py-4 whitespace-nowrap">
                  <div className="text-sm text-gray-500">
                    {op.completionDate?.toDate?.()?.toLocaleDateString() || 'N/A'}
                  </div>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
        
        {completedOperations.length === 0 && (
          <div className="text-center py-12">
            <FiCheckCircle className="mx-auto h-12 w-12 text-gray-400" />
            <h3 className="mt-2 text-sm font-medium text-gray-900">No completed operations</h3>
            <p className="mt-1 text-sm text-gray-500">Completed operations will appear here once they're finished.</p>
          </div>
        )}
      </div>
    </div>
  );
}